
import { createSlice } from '@reduxjs/toolkit';
// jwtUtil     /   .
import { getInitialUserState, setToken, removeToken, parseJwt } from "../../util/jwtUtil"; 

// 1.   
//         .
const initialState = getInitialUserState();

export const authSlice = createSlice({
  name: 'auth', //  Slice 
  initialState,
  reducers: {
    /**
     * @name loginSuccess
     * @description    ,       .
     * @param {Object} action.payload - { token: 'jwt-string', userPayload: {id: 1, name: ''}}    
     */
    loginSuccess: (state, action) => {
      const { token, userPayload } = action.payload;
      
      // 1. Redux  
      state.user = userPayload; 
      state.isAuthenticated = true;

      // 2.    ()
      setToken(token); // jwtUtil  setToken  
    },

    /**
     * @name logout
     * @description  ,  null    .
     */
    logout: (state) => {
      // 1. Redux  
      state.user = null;
      state.isAuthenticated = false;

      // 2.    ()
      removeToken(); // jwtUtil  removeToken  
    },

    /**
     * @name setUserFromToken
     * @description      (   )   .
     */
    setUserFromToken: (state, action) => {
        //    initialState = getInitialUserState()  , 
        //     ,     .
        const { userPayload } = action.payload;
        state.user = userPayload;
        state.isAuthenticated = true;
    }
  },
});

//      .
export const { loginSuccess, logout, setUserFromToken } = authSlice.actions;

//   Store   .
export default authSlice.reducer;